import Template from './template'
import OHLCTemplate from './ohlc_template'
import Stacked from './stacked'
import config from '../../../../config'

export const PREFIX = '\n𝗕𝗹𝗲𝗽 '

// client side indicators that don't need anything from Blep server
const clientIndicators = [
	['BinanceOI', 'Open Interest', '!binance_BTCUSDT_openinterest', '!binance_ETHUSDT_asdf'],
]

export default async function (PineJS) {
	const url = new URL(`${config.apiUrl}/v1/reference/indicators`)

	const indicators = await fetch(url).then(res => res.json())

	return indicators
		.concat(clientIndicators).map(ind => Template({
			name: ind[0],
			description: ind[1],
			plotNames: ind.slice(2),
		})(PineJS))
		.concat([OHLCTemplate({
			name: 'namee',
			description: 'descriptiones',
			plotNames: [],
		})(PineJS)])
		.concat([Stacked({
			name: 'Ethereum Global Open Interest',
			description: 'ETH Global Open Interest',
			tickers: ['OKEX,ETH-USD_SWAP,openinterest,1m', 'Bybit,ETHUSD,openinterest,1m', 'Huobi,ETH-USD,openinterest,1m', 'Bitmex,ETHUSD,openinterest,1m'], //, 'Deribit,ETH-PERPETUAL,openinterest,1m'],
		})(PineJS)])
		.concat([Stacked({
			name: 'Bitcoin Global Open Interest',
			description: 'BTC Global Open Interest',
			tickers: ['OKEX,BTC-USD_SWAP,openinterest,1m', 'Bybit,BTCUSD,openinterest,1m', 'Huobi,BTC-USD,openinterest,1m', 'Bitmex,BTCUSD,openinterest,1m'], //, 'Deribit,BTC-PERPETUAL,openinterest,1m'],
		})(PineJS)])
		.map(load)
}

// prefix descriptions with \nBlep to show at beginning of TV indicator list.
function load (indicator) {
	indicator.metainfo.description = PREFIX + indicator.metainfo.description
	return indicator
}

