import { checkInterval, getKlines, getSpotSymbols, getFutsSymbols, getExchangeServerTime, getInterval } from './helpers'
import BinanceWebsockets from './websockets'

const configurationData = {
	supports_marks: false,
	supports_timescale_marks: false,
	supports_time: true,
	supported_resolutions: [
		'1', '3', '5', '15', '30', '60', '120', '240', '1D', '3D', '1W', '1M',
	],
}

export default class BinanceAdapter {
	constructor (params) {
		this.subscriptions = {}
		this.realtimeEnabled = params.realtimeEnabled
		return this
	}
	// get a configuration of your datafeed (e.g. supported resolutions, exchanges and so on)
	/* onReady (callback) {
		console.log('[onReady]: Method call')
		setTimeout(() => callback(configurationData)) // callback must be called asynchronously.
	} */

	async searchSymbols (input, exchange, symbolType) {
		if (!['binance', ''].includes(exchange)) return []

		const spot = await getSpotSymbols()
		const futs = await getFutsSymbols()

		let symbols = []

		symbols = symbols.concat(spot.map(item => ({
			symbol: item.symbol,
			ticker: `!binance_${item.symbol}`,
			full_name: `Binance:${item.symbol}`,
			description: item.symbol,
			exchange: 'binance',
			type: 'bitcoin',
		})))

		symbols = symbols.concat(futs.map(item => ({
			symbol: `${item.symbol}`,
			ticker: `!binance_${item.symbol}`,
			full_name: `Binance:${item.symbol}`,
			description: `${item.symbol} Perpetual Futures`,
			exchange: 'binance',
			type: 'bitcoin',
		})))

		return symbols
	}

	// retrieve information about a specific symbol (exchange, price scale, full symbol etc.)
	async resolveSymbol (ticker, cb, cberr) {
		console.log('[binance resolveSymbol]: Method call', ticker)
		const comps = ticker.split('_')
		const symbolName = (comps.length > 1 ? comps[1] : ticker).toUpperCase()

		// need for pricescale()
		function pricescale (symbol) {
			for (const filter of symbol.filters) {
				if (filter.filterType === 'PRICE_FILTER') {
					return Math.round(1 / parseFloat(filter.tickSize))
				}
			}
			return 1
		}

		const symbolInfo = (symbol, isPerp) => ({
			name: symbol.symbol,
			description: symbol.baseAsset + symbol.quoteAsset + (isPerp ? ' Perpetual Futures' : ''),
			ticker: `!binance_${symbol.symbol}`,
			exchange: 'Binance',
			// listed_exchange: 'Binance',
			// type: 'crypto',
			session: '24x7',
			minmov: 1,
			pricescale: pricescale(symbol), // or 100
			timezone: 'Etc/UTC',
			has_intraday: true,
			intraday_multipliers: configurationData.supported_resolutions,
			has_daily: true,
			has_weekly_and_monthly: true,
			currency_code: symbol.quoteAsset,
			data_status: 'streaming',
		})

		// Get symbols
		let symbol = (await getSpotSymbols()).find(i => i.symbol === symbolName)
		if (symbol) {
			cb(symbolInfo(symbol))
			return
		}

		symbol = (await getFutsSymbols()).find(i => i.symbol === symbolName) // slice off PERP suffix
		if (symbol) {
			cb(symbolInfo(symbol, true))
			return
		}

		cberr('[resolveSymbol]: symbol not found')
	}

	// get historical data for the symbol
	async getBars (symbolInfo, interval, from, to, onHistoryCallback, onErrorCallback, firstDataRequest) {
		console.log('[binance getBars] Method call', symbolInfo, interval)
		console.log('[binance getBars] First request', firstDataRequest)

		if (!checkInterval(interval)) {
			return onErrorCallback('[getBars] Invalid interval')
		}

		const comps = symbolInfo.ticker.split('_')
		const symbolName = (comps.length > 1 ? comps[1] : symbolInfo.ticker).toUpperCase()

		const klines = await getKlines({ symbol: symbolName, interval, from, to })
		if (klines.length > 0) {
			return onHistoryCallback(klines)
		}

		onErrorCallback('Klines data error')
	}

	// subscription to real-time updates
	subscribeBars (symbolInfo, interval, callback, key, onResetCacheNeededCallback) {
		if (key === undefined) return
		console.log('[subscribeBars]: Method call with subscribeUID:', key)

		const comps = symbolInfo.ticker.split('_')
		let symbolName = (comps.length > 1 ? comps[1] : symbolInfo.ticker).toUpperCase()

		const isPerp = symbolName.includes('PERP')
		symbolName = isPerp ? symbolName.slice(0, -4) : symbolName

		if (!this.ws) {
			this.wsListeners()
		}

		const streamName = `${symbolName.toLowerCase()}@kline_${getInterval(interval)}`

		const sub = {
			symbolInfo, interval, callback, streamName, isPerp,
		}

		if (this.realtimeEnabled) this.ws.subscribe(streamName, isPerp)
		this.subscriptions[key] = sub
	}

	unsubscribeBars (key) {
		console.log('[unsubscribeBars]: Method call with subscriberUID:', key)
		const sub = this.subscriptions[key]

		const streamName = `${sub.symbolInfo.name.toLowerCase()}@kline_${getInterval(sub.interval)}`
		this.ws.unsubscribe(streamName, sub.isPerp)
		delete this.subscriptions[key]
	}

	getServerTime (callback) {
		getExchangeServerTime().then(time => {
			callback(Math.floor(time / 1000))
		}).catch(err => {
			console.error(err)
		})
	}

	wsListeners () {
		if (!this.realtimeEnabled) return
		this.ws = new BinanceWebsockets()
		this.ws.on('kline', (stream, k, isPerp) => {
			const sub = Object.values(this.subscriptions).find(s => s.streamName === stream && s.isPerp === isPerp)
			// sometimes messages come in after unsub because binance hasn't processed it yet.
			sub && sub.callback({
				open: parseFloat(k.o),
				close: parseFloat(k.c),
				high: parseFloat(k.h),
				low: parseFloat(k.l),
				volume: parseFloat(k.v),
				time: k.t,
			})
		})
	}
}

