import colors from './colors'

export default ({ name, description, tickers }) => {
	const numPlots = tickers.length
	const range = [...Array(numPlots).keys()] // [1,2,3,4,5...]

	name = name || 'Stacked Area'
	description = description || 'Stacked Area'
	const shortDescription = name || 'Stacked'
	const inputs = range.map(i => ({
		id: `in_${i}`,
		name: `Symbol ${i}`,
		defval: tickers[i],
		type: 'symbol',
	}))
	const plots = range.map(i => ({
		id: `plot_${i}`,
		type: 'line',
	}))

	const defaultStyles = plots.reduce((prev, cur, i) => {
		prev[`plot_${i}`] = {
			linestyle: 0,
			linewidth: 1,
			plottype: 4,
			trackPrice: false,
			transparency: 0, // 100 - 100/numPlots,
			visible: true,
			color: colors(i), // '#0496ff',
		}
		return prev
	}, {})

	return (PineJS) => ({
		name,
		metainfo: {
			_metainfoVersion: 42,
			id: `${name}@tv-basicstudies-1`,
			name,

			description,
			shortDescription,
			scriptIdPart: '',

			is_hidden_study: false,
			is_price_study: false,
			isCustomIndicator: true,

			isTVScript: false,
			isTVScriptStub: false,
			inputs,

			plots,

			defaults: {
				styles: defaultStyles,
				precision: 4,
				inputs: range.reduce((prev, cur, i) => {
					prev[`in_${i}`] = tickers[i]
					return prev
				}, {}),
			},

			styles: plots.reduce((prev, cur, i) => {
				prev[`plot_${i}`] = {
					title: cur,
					histogramBase: 0,
				}
				return prev
			}, {}),
			// filledAreas: [{
			// id: 'fill_0',
			// objAId: 'plot_3',
			// objBId: 'plot_4',
			// type: 'plot_plot',
			// title: 'Plots Background',
			// isHidden: !1,
			// palette: 'palette_0',
			// }],

		},
		constructor: function () {
			this.init = function (context, input) {
				this._context = context
				this._input = input

				const period = PineJS.Std.period(this._context)

				range.forEach(i => {
					const input = this._input(i)
					// if (input) {
					this._context.new_sym(input, period, period)
					// }
				})
			}

			this.main = function (context, input) {
				this._context = context
				this._input = input

				this._context.select_sym(0)
				var mainSymbolTimeSeries = this._context.new_var(this._context.symbol.time)

				// Get closeSeries for every input symbol
				const closeSeries = range.map(i => {
					this._context.select_sym(i + 1)

					var timeSeries = this._context.new_var(this._context.symbol.time)
					var closeSeries = this._context.new_var(PineJS.Std.close(this._context))
					return closeSeries.adopt(timeSeries, mainSymbolTimeSeries)
				})

				// Stack the closeSeries
				for (let i = 1; i < closeSeries.length; i++) {
					closeSeries[i] += closeSeries[i - 1]
				}

				this._context.select_sym(0)

				// Reverse it to draw area without covering others
				return closeSeries.reverse()
			}
		},
	})
}
