const plotType = {
	Histogram: 1,
	Line: 2,
	Cross: 3,
	Area: 4,
	Columns: 5,
	Circles: 6,
	LineWithBreaks: 7,
	AreaWithBreaks: 8,
	OHLCCandles: 'ohlc_candles',
	OHLCBars: 'ohlc_bars',
	OHLCOpen: 'ohlc_open',
	OHLCHigh: 'ohlc_high',
	OHLCLow: 'ohlc_low',
	OHLCClose: 'ohlc_close',
	OHLCColorer: 'ohlc_colorer',
	BGColorer: 'bg_colorer',
	BarColorer: 'bar_colorer',
}

const inputType = {
	Integer: 'integer',
	Float: 'float',
	Bool: 'bool',
	Text: 'text',
	Symbol: 'symbol',
	Source: 'source',
	Resolution: 'resolution',
}

export default ({ name, description, plotNames }) => {
	return (PineJS) => ({
		name,
		metainfo: {
			_metainfoVersion: 42,

			id: `${name}@tv-basicstudies-1`,

			name,

			// \n prefix pushes this custom indicator about tradingview built-in indicators
			description,
			shortDescription: name,
			scriptIdPart: '',

			is_price_study: false,
			is_hidden_study: false,
			isCustomIndicator: true,

			isTVScript: false,
			isTVScriptStub: false,
			inputs: [{
				id: 'input1',
				type: inputType.Symbol,
				name: 'input 1 name',
			}],

			ohlcPlots: {
				plotcandle_0: {
					title: 'Plot candle title', // required. Title of plot, might be used in settings dialog
				},
			},

			plots: [{
				id: 'plot_0',
				type: 'ohlc_open',
				target: 'plotcandle_0',
			},
			{
				id: 'plot_1',
				type: 'ohlc_high',
				target: 'plotcandle_0',
			},
			{
				id: 'plot_2',
				type: 'ohlc_low',
				target: 'plotcandle_0',
			},
			{
				id: 'plot_3',
				type: 'ohlc_close',
				target: 'plotcandle_0',
			},
			{
				id: 'plot_4',
				type: 'ohlc_colorer',
				palette: 'palette_0',
				target: 'plotcandle_0',
			}],

			palettes: {
				palette_0: {
					colors: [
						{ name: 'Up Color' },
						{ name: 'Down Color' },
					],
					valToIndex: {
						1: 0,
						'-1': 1,
					},
				},
			},

			// plots: plotNames.map((n, i) => ({ id: `plot_${i}`, type: 'line' })),

			defaults: {
				ohlcPlots: {
					plotcandle_0: {
						borderColor: '#000000',
						color: '#2196F3',
						drawBorder: true,
						drawWick: true,
						plottype: plotType.OHLCCandles,
						visible: true,
						wickColor: '#737375',
					},
				},
				palettes: {
					palette_0: {
						colors: [
							{ color: 'rgba( 107, 165, 131, 1)' },
							{ color: 'rgba( 215, 84, 66, 1)' },
						],
					},
				},
				styles: {},
				precision: 'default',
				inputs: {
					input1: '!binance_BTCUSDT',
				},
			},
			styles: {
				plotcandle_0: {
					// this doesn't work.
					// upColor: 'rgba( 107, 165, 131, 1)',
					// downColor: 'rgba( 215, 84, 66, 1)',
					// drawWick: !0,
					// drawBorder: !0,
					// borderColor: 'rgba( 55, 134, 88, 1)',
					// borderUpColor: 'rgba( 34, 84, 55, 1)',
					// borderDownColor: 'rgba( 91, 26, 19, 1)',
					// wickColor: 'rgba( 115, 115, 117, 1)',
					// wickUpColor: 'rgba( 115, 115, 117, 1)',
					// wickDownColor: 'rgba( 115, 115, 117, 1)',
					// barColorsOnPrevClose: !1,
				},
			},
		},
		constructor: function () {
			// very helpful doc: https://github.com/tradingview/charting_library/issues/4732

			// called upon loading the indicator. new_sym() retrieves symbol from server into context.
			this.init = function (context, input) {
				this._context = context
				this._input = input

				const period = PineJS.Std.period(this._context)

				// https://github.com/tradingview/charting_library/issues/4019
				// let ticker = context.symbol.ticker
				// ticker = ticker.substring(1, ticker.length - 2)

				this._context.new_sym(this._input(0), period, period)
			}

			this.main = function (context, input) {
				this._context = context
				this._input = input

				this._context.select_sym(1)

				var open = PineJS.Std.open(this._context)
				var high = PineJS.Std.high(this._context)
				var low = PineJS.Std.low(this._context)
				var close = PineJS.Std.close(this._context)
				var direction = Math.sign(close - open)

				return [open, high, low, close, direction]
				// return [open, high, low, close, direction]
				// this._context.select_sym(0)
				// var mainSymbolTimeSeries = this._context.new_var(this._context.symbol.time)

				// // important part: use adopt to find proper value inside series
				// // thus, let's look at firstSeriesCloseSeries.adopt(firstSymbolTimeSeries, mainSymbolTimeSeries)
				// // here we trying to find proper close value of the first series, "adopting" it to the main series' time
				// const res = plotNames.map((_, i) => {
				// // switch to the first requested symbol
				// this._context.select_sym(i + 1)
				// // save the first series' time and close value
				// var timeSeries = this._context.new_var(this._context.symbol.time)
				// var closeSeries = this._context.new_var(PineJS.Std.close(this._context))
				// return closeSeries.adopt(timeSeries, mainSymbolTimeSeries)
				// })
				// this._context.select_sym(0)
				// return res
			}
		},
	})
}
