import Flatten from 'lodash/flatten'
import EventEmitter from 'eventemitter3'

const endpoint = 'wss://stream.binance.com:9443/stream'
const endpointf = 'wss://fstream.binance.com/stream'

export default class BinanceWebsockets extends EventEmitter {
	constructor (params) {
		super()
		this.subscriptions = []
		this.subscriptionsf = []
		this.ws = null
		this.wsf = null
		console.log('Binance WebSocket class initialized..')
		this.connect()
		this.connectf()
	}

	subscribe (channels, isPerp) {
		if (!isPerp) {
			// Add to our list of subscriptions:
			this.subscriptions.push([channels])
			this.subscriptions = Flatten(this.subscriptions)
			// If these are additional subscriptions, only send the new ones:
			if (this.connected) this.sendSubscriptions(Flatten([channels]), this.ws)
		} else {
			this.subscriptionsf.push([channels])
			this.subscriptionsf = Flatten(this.subscriptionsf)
			if (this.connectedf) this.sendSubscriptions(Flatten([channels]), this.wsf)
		}
	}

	unsubscribe (channel, isPerp) {
		if (!isPerp) {
			this.subscriptions = this.subscriptions.filter(s => channel !== s)
			if (this.connected) this.sendUnsubscriptions([channel], this.ws)
		} else {
			this.subscriptionsf = this.subscriptionsf.filter(s => channel !== s)
			if (this.connectedf) this.sendUnsubscriptions([channel], this.wsf)
		}
	}

	connect () {
		this.connected = false
		this.ws = new WebSocket(endpoint)
		this.ws.onopen = this.onOpen.bind(this)
		this.ws.onclose = this.onDisconnect.bind(this)
		this.ws.onerror = this.onError.bind(this)
		this.ws.onmessage = this.onMessage.bind(this)
	}

	connectf () {
		this.connectedf = false
		this.wsf = new WebSocket(endpointf)
		this.wsf.onopen = this.onOpenf.bind(this)
		this.wsf.onclose = this.onDisconnectf.bind(this)
		this.wsf.onerror = this.onError.bind(this)
		this.wsf.onmessage = this.onMessagef.bind(this)
	}

	onOpen () {
		// Authenticate:
		// this.ws.send(`{"action":"auth","params":"${this.apiKey}"}`)
		this.connected = true
		this.sendSubscriptions(this.subscriptions, this.ws)
	}

	onOpenf () {
		this.connectedf = true
		this.sendSubscriptions(this.subscriptionsf, this.wsf)
	}

	sendSubscriptions (subscriptions, ws) {
		if (subscriptions.length === 0) return
		const req = {
			method: 'SUBSCRIBE',
			id: 1,
			params: [
				...subscriptions,
			],
		}
		ws.send(JSON.stringify(req))
	}

	sendUnsubscriptions (subscriptions, ws) {
		const req = {
			method: 'UNSUBSCRIBE',
			id: 1,
			params: [
				...subscriptions,
			],
		}
		ws.send(JSON.stringify(req))
	}

	onDisconnect () {
		setTimeout(this.connect.bind(this), 2000)
	}

	onDisconnectf () {
		setTimeout(this.connectf.bind(this), 2000)
	}

	onError (e) {
		console.log('Error:', e)
	}

	onMessage (msg) {
		let data = msg.data
		data = JSON.parse(data)
		if (data.id !== undefined) return
		this.emit(data.data.e, data.stream, data.data.k, false)
	}

	onMessagef (msg) {
		let data = msg.data
		data = JSON.parse(data)
		if (data.id !== undefined) return
		this.emit(data.data.e, data.stream, data.data.k, true)
	}
}
