import Debounce from 'lodash/debounce'
import Fuse from 'fuse.js'

import * as external from './external'
import BlepWebsockets from './websockets'
import config from '../../../../config'
// import BlepEventSource from './sse.js'

const BASE_URL = config.apiUrl
const POLL_INTERVAL = 15 // seconds

export default class BlepAdapter {
	/**
	 *  Blep Adapter
	 *  @param  {Object} params         Object containing { apikey }
	 *  @return {BlepAdapter}        return created instance for chaining methods
	 */
	constructor (params) {
		this.subscriptions = {}
		this.apiKey = params.apiKey
		this.realtimeEnabled = params.realtimeEnabled
		this.searchSymbols = Debounce(this._searchSymbols, 250, { trailing: true })
		return this
	}

	/**
	 *  onReady method for TV lib
	 *  @param {Function} cb Callback when we are ready
	 *  @return {null}
	 */
	onReady (cb) {
		if (this.realtimeEnabled) {
			this.wsListeners()
			// this.sseListeners()
		} else {
			setInterval(this.onInterval.bind(this), POLL_INTERVAL * 1000)
		}
		const res = {
			supports_time: true,
			supports_marks: false,
			supports_timescale_marks: false,
			exchanges: [{
				value: '',
				name: 'All',
				desc: 'all',
			}, {
				value: 'blep',
				name: 'Blep',
				desc: 'BlepDATA',
			}, {
				value: 'binance',
				name: 'Binance',
				desc: 'Binance',
			}],
			symbols_types: [{
				name: 'All',
				value: '',
			}, {
				name: 'Standard',
				value: 'standard',
			}, {
				name: 'Blockchain',
				value: 'blockchain',
			}],
		}
		cb(res)
	}

	/**
	 *  On each interval we loop through our subscriptions and request bars for the past 2min
	 *  @return {null}
	 */
	onInterval () {
		const now = Date.now()
		for (const id in this.subscriptions) {
			const sub = this.subscriptions[id]
			this.getBars(sub.symbolInfo, sub.interval, ((now - 120 * 1000) / 1000), (now / 1000), (ticks) => {
				if (ticks.length === 0) return
				ticks.reverse().forEach(sub.callback)
			})
		}
	}

	/**
	 *  Debounced searchSymbols method for TV lib
	 *  @param  {String}   input      Users search input
	 *  @param  {String}   exchange   Exchange search input
	 *  @param  {String}   symbolType Symbol type ( `stock`, `bitcoin`, `forex`)
	 *  @param  {Function} cb         Callback for returning results
	 *  @return {null}
	 */
	async _searchSymbols (input, exchange, symbolType, cb) {
		try {
			const url = new URL(`${BASE_URL}/v1/reference/symbols`)
			url.searchParams.set('apikey', this.apiKey)
			url.searchParams.set('search', input)
			url.searchParams.set('exchange', exchange)
			url.searchParams.set('type', symbolType)

			let res = await fetch(url).then(res => res.json())
			cb(res)

			const externalRes = await external.searchSymbols(...arguments)

			res = res.concat(externalRes)
			cb(res)

			// fuzzysearch
			if (input !== '') {
				const fuse = new Fuse(res, { keys: ['symbol', 'ticker'] })
				res = fuse.search(input).map(x => x.item)
			}
			cb(res)
		} catch (err) {
			console.log('not found:', err)
			const r = []
			cb(r)
		}
	}

	/**
	 *  Resolving a symbol simply gets the company info for this symbol
	 *  @param  {String}   symbol Symbol string we are requesting
	 *  @param  {Function} cb     Callback for symbol info
	 *  @param  {Function}   cberr  Callback for errors occured
	 *  @return {null}
	 */
	async resolveSymbol (symbolName, cb, cberr) {
		// console.log('resolve symbol:', symbolName)

		// Handle ! prefix to query external APIs
		if (symbolName[0] === '!') {
			external.resolveSymbol(...arguments)
			return
		}

		const url = new URL(`${BASE_URL}/v1/reference/symbols/${symbolName}`)
		url.searchParams.set('apikey', this.apiKey)

		try {
			const symbolInfo = await fetch(url).then(res => res.json())
			cb(symbolInfo)
		} catch (err) {
			cberr(err)
		}
	}

	/**
	 *  Get aggregate bars for our symbol
	 *  @param  {Object}   symbolInfo   Object returned from `resolveSymbol`
	 *  @param  {String}   resolution   Interval size for request ( `1`, `1D`, etc )
	 *  @param  {Int}   from         Unix timestamp to search from
	 *  @param  {Int}   to           Unix timestamp to search to
	 *  @param  {Function} cb           Callback with resolved bars
	 *  @param  {Function}   cberr        Callback for errors
	 *  @param  {Boolean}   firstRequest If this is the first request for this symbol
	 *  @return {null}
	 */
	async getBars (symbolInfo, resolution, from, to, cb, cberr, firstRequest) {
		// console.log('getBars() called', symbolInfo.ticker)

		// Handle ! prefix to query external APIs
		if (symbolInfo.ticker[0] === '!') {
			external.getBars(...arguments)
			return
		}

		try {
			const url = new URL(`${BASE_URL}/v1/history`)
			url.searchParams.set('apikey', this.apiKey)
			url.searchParams.set('symbol', symbolInfo.ticker)
			url.searchParams.set('from', from * 1000)
			url.searchParams.set('to', to * 1000)
			url.searchParams.set('resolution', resolution)

			const res = await fetch(url).then(res => res.json())
			const bars = res.map(t => ({
				time: t.time,
				close: t.close,
				open: t.open,
				high: t.high,
				low: t.low,
				volume: t.volume,
			}))
			return cb(bars, { noData: (bars.length === 0) })
		} catch (err) {
			console.log(err)
			// cberr(err)
		}
	}

	/**
	 *  Subscribe to future updates for this symbol
	 *  @param  {Object}   symbolInfo Object returned from `resolveSymbol`
	 *  @param  {String}   interval   Interval size for request
	 *  @param  {Function} cb         Callback when we have new bars
	 *  @param  {String}   key        Unique key for this subscription
	 *  @return {null}
	 */
	subscribeBars (symbolInfo, interval, cb, key) {
		const sub = {
			symbolInfo: symbolInfo,
			interval: interval,
			callback: cb,
		}

		if (symbolInfo.ticker[0] === '!') {
			external.subscribeBars(...arguments)
			return
		}

		// Currently only allow minute subscriptions:
		if (sub.interval !== '1') return
		if (this.realtimeEnabled) this.ws.subscribe(`AM.${symbolInfo.ticker}`)
		// if (this.realtimeEnabled) this.eventSource.subscribe(`AM.${symbolInfo.ticker}`)
		this.subscriptions[key] = sub
	}

	/**
	 *  Unsubscribe from future updates for a symbol
	 *  @param  {String} key Unique key for this subscription
	 *  @return {null}
	 */
	unsubscribeBars (key) {
		external.unsubscribeBars(key)

		if (key in this.subscriptions) { delete this.subscriptions[key] }
	}

	async getServerTime (cb) {
		const url = new URL(`${BASE_URL}/v1/time`)
		try {
			const res = await fetch(url).then(res => res.json())
			const serverTime = res.ServerTime / 1E9
			cb(serverTime)
		} catch (err) {
			console.log(err)
		}
	}

	/**
	 *  Add the websocket listeners and start the connection:
	 *  @return {null}
	 */
	wsListeners () {
		if (!this.realtimeEnabled) return
		this.ws = new BlepWebsockets({ apiKey: this.apiKey })
		this.ws.on('AM', (aggMin) => {
			for (const id in this.subscriptions) {
				const sub = this.subscriptions[id]
				sub.callback({
					open: aggMin.o,
					close: aggMin.c,
					high: aggMin.h,
					low: aggMin.l,
					volume: aggMin.v,
					time: aggMin.s,
				})
			}
		})
	}

	/*
	sseListeners () {
		if (!this.realtimeEnabled) return
		this.eventSource = new BlepEventSource('sse')
		this.eventSource.onmessage = e => {
			const data = JSON.parse(e.data)

			this.subscriptions.forEach(sub => {
				sub.callback({
					open: data.o,
					close: data.c,
					high: data.h,
					low: data.l,
					volume: data.v,
					time: data.s,
				})
			})
		}
	}
	*/
}

