const spotAPI = `https://api.binance.com/api`
const futsAPI = `https://fapi.binance.com/fapi`

// {symbol: raw info from binance}
let spotSymbolsCache = []
let futsSymbolsCache = []

const intervals = {
	1: '1m',
	3: '3m',
	5: '5m',
	15: '15m',
	30: '30m',
	60: '1h',
	120: '2h',
	240: '4h',
	360: '6h',
	480: '8h',
	720: '12h',
	D: '1d',
	'1D': '1d',
	'3D': '3d',
	W: '1w',
	'1W': '1w',
	M: '1M',
	'1M': '1M',
}

export async function getExchangeServerTime () {
	try {
		const url = new URL(`${spotAPI}/v1/time`)

		const res = await fetch(url).then(res => res.json())
		return res.serverTime
	} catch (err) {
		console.log(err)
	}
}

export async function getSymbols () {
	return [await getSpotSymbols(), await getFutsSymbols()]
}

export async function getSpotSymbols () {
	if (spotSymbolsCache.length === 0) {
		try {
			const url = new URL(`${spotAPI}/v1/exchangeInfo`)
			const res = await fetch(url).then(res => res.json())
			spotSymbolsCache = res.symbols
		} catch (err) {
			console.log('not found:', err)
			return []
		}
	}

	return spotSymbolsCache
}

export async function getFutsSymbols () {
	if (futsSymbolsCache.length === 0) {
		try {
			const url = new URL(`${futsAPI}/v1/exchangeInfo`)
			const res = await fetch(url).then(res => res.json())
			futsSymbolsCache = res.symbols.map(s => {
				s.symbol += 'PERP' // add perp suffix
				return s
			})
		} catch (err) {
			console.log('not found:', err)
			return []
		}
	}

	return futsSymbolsCache
}

const formatingKline = ([openTime, open, high, low, close, volume, closeTime, qav, numTrades, baseVolume, quoteVolume]) => {
	return {
		time: openTime,
		open: parseFloat(open),
		high: parseFloat(high),
		low: parseFloat(low),
		close: parseFloat(close),
		volume: parseFloat(volume),
	}
}

export async function getKlines ({ symbol, interval, from, to }) {
	console.log('getKlines', symbol)
	const isPerp = symbol.includes('PERP')
	symbol = isPerp ? symbol.slice(0, -4) : symbol

	interval = intervals[interval]
	from *= 1000
	to *= 1000

	let bars = []

	let res
	do {
		// loop through requests from new to old historical data.
		const url = new URL(`${isPerp ? futsAPI : spotAPI}/v1/klines`)
		url.searchParams.set('symbol', symbol.toUpperCase())
		url.searchParams.set('interval', interval)
		// url.searchParams.set('startTime', from)
		url.searchParams.set('endTime', to)
		url.searchParams.set('limit', 1000)

		try {
			res = await fetch(url).then(res => res.json())
		} catch (err) {
			console.log(err)
		}

		if (!res.length) break

		bars = [...res.map(i => formatingKline(i)), ...bars]
		to = res[0][0]
	} while (res[0][0] > from)

	return bars
}

export const checkInterval = (interval) => !!intervals[interval]

export function getInterval (interval) {
	return intervals[interval]
}
