import BinanceAdapter from './binance'

const exchangeMap = {
	'!binance': new BinanceAdapter({ realtimeEnabled: true }),
}

// search across all external data sources
export async function searchSymbols (input, exchange, symbolType) {
	let symbols = Object.values(exchangeMap).map(x => x.searchSymbols(...arguments))
	symbols = await Promise.all(symbols)
	return [].concat(...symbols)
}

// resolve symbol across single data source with the ticker namespace
export function resolveSymbol (ticker, cb, cberr) {
	const exchange = getExchange(ticker)
	exchange.resolveSymbol(...arguments)
}

export async function getBars (symbolInfo, resolution, from, to, cb, cberr, firstRequest) {
	const exchange = getExchange(symbolInfo.ticker)
	exchange.getBars(...arguments)
}

export function subscribeBars (symbolInfo, interval, cb, key) {
	const exchange = getExchange(symbolInfo.ticker)
	exchange.subscribeBars(...arguments)
}

// key is just ticker_timeframe so getExchange still works.
// NOTE: this is undocumented and may break
export function unsubscribeBars (key) {
	const ticker = key
	const exchange = getExchange(ticker)
	exchange && exchange.unsubscribeBars(key)
}

function getExchange (ticker) {
	const ns = ticker.split('_')[0]
	return exchangeMap[ns]
}
